.container__head {
  display: block;
  height: auto;
  min-height: 100%;
}

.container__prez {
  display: flex;
  justify-content: space-between;
  flex-flow: column;
  height: auto;
  min-height: 100%;
  margin-left: 50px;
  margin-right: 50px;
  font-family: "Montserrat";
  &--title {
    text-align: center;
    font-family: "Montserrat";
    font-weight: 700;
    height: 150px;
    & h1 {
      margin: 50px auto;
      justify-self: center;
      font-weight: 400;
      font-size: 50px;
    }
  }
  &--model {
    display: flex;
    flex-flow: row;
    justify-content: space-evenly;
    margin-top: 50px;
    margin-bottom: 50px;
  }

  &--list {
    list-style-type: none;
    text-align: center;
    line-height: 40px;
    width: 200px;
    & button {
      border: none;
      background-color: transparent;
      min-width: 200px;
      cursor: pointer;
    }
    & h2 {
      background-color: $fourthColor;
      border-radius: 10px;
      border-top-left-radius: 0px;
      color: white;
      font-family: "Montserrat";
      margin: 0;
      height: 40px;
      line-height: 40px;
    }
    & p {
      text-decoration: none;
      font-family: "Montserrat";
      color: $thirdColor;
      font-size: 20px;
      margin: 0;
      font-weight: bold;
    }
  }
}

.container__prez {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  &--text {
    width: 95%;
    background-color: $fifthColor;
    text-align: justify;
    font-weight: 300;
    font-family: "Montserrat";
    font-size: 28px;
    color: white;
    padding: 20px;
    min-height: 500px;
    margin-left: 100px;
  }
  &--image {
    width: 100%;
    height: 100%;
    min-height: 500px;
    padding: 20px;
    align-content: center;
    object-fit: cover;
    color: white;
    font-weight: 500;
    font-family: "Montserrat";
    font-size: 30px;
    text-align: center;

    margin-right: 5px;
  }
}
.container__feat {
  display: flex;
  flex-flow: column;
  margin-bottom: 100px;
  background-color: $fifthColor;
  margin-top: 100px;
  height: auto;
  font-size: 20px;
  line-height: 40px;
  padding-left: 30px;
  &--button {
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
  &--title {
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    line-height: 50px;
    height: 50px;
  }
  &--h3 {
    margin: 0;
    font-size: 30px;
    color: white;
  }
  & i {
    height: 50px;
    line-height: 50px;
    margin-right: 50px;
    font-size: 30px;
    color: white;
  }
  &--list {
    margin-left: 100px;
    margin-bottom: 20px;
    & li {
      list-style-type: none;
      font-size: 30px;
    }
  }
}
