html,
body,
#root {
  width: 100%;
  height: 100%;
  min-height: 100%;
  margin: 0px;
  padding: 0px;
  font-family: "Montserrat";
}
body {
  background-color: transparentize($color: rgb(23, 23, 72), $amount: 0.7);
}
.body {
  display: block;
  height: 100%;
  min-height: 100%;
  padding-bottom: 30px;
}
a {
  text-decoration: none;
  &:hover {
    cursor: pointer;
  }
}
.mainHome,
.mainProduct,
.mainContact {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  display: contents;
  min-height: 100%;
  width: 100%;
}

.container__head,
.mainHome__text {
  background-image: url(/src/Assets/Fond.webp);
  background-size: 150%;
}
